@charset "UTF-8";

.sitemap-sect {
	padding-top: 30px;
	padding-bottom: calc(12vw + 120px);

	@include mq(sm2) {
		margin: 0 0 80px;
		padding: 40px $sp-padding 12vw;
	}

	&-row {
		border-bottom: 1px solid #fff;
		font-size: 17px;
		padding: 20px 0;
		max-width: 780px;

		@include mq(sm2) {
			font-size: vw2(15);
		}

		&:first-child {
			border-top: 1px solid #fff;
		}

		> ul {
			display: flex;
			flex-wrap: wrap;
			gap: 13px 40px;
			margin-bottom: 15px;

			@include mq(sm2) {
				gap: 20px 0;
			}

			&:last-child {
				margin-bottom: 0;
			}
			> li {
				font-weight: 500;

				@include mq(sm2) {
					width: 50%;
				}
				.child-list {
					display: block;
					margin: 0;
					> li {
						@include mq(sm2) {
							font-size: vw2(14);
						}
						a {
							font-weight: 400;
							position: relative;
							padding-left: 15px;
							&:before {
								content: "";
								display: block;
								width: 5px;
								height: 1px;
								background: #fff;
								position: absolute;
								left: 0;
								top: 12px;
							}
						}
					}
				}
			}
		}
	}
}
