@charset "UTF-8";

.entry-form {
	margin: 120px 0;
	padding-top: 60px;
	padding-bottom: 100px;

	background: #2b5d55;
	background: linear-gradient(90deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 40px 0 20px;
		padding: 50px $sp-padding 10px;
	}

	&-inner {
		max-width: 720px;
		margin: 0 auto;
	}

	&__lead {
		padding-bottom: 40px;
		margin-bottom: 40px;
		text-align: center;
	}
	&__ttl {
		padding-bottom: 20px;
		margin-bottom: 40px;
		border-bottom: 1px solid #fff;
	}

	&-nav {
		margin-bottom: 80px;
		&__list {
			display: flex;
			flex-wrap: wrap;
			gap: 30px;

			@include mq(sm2) {
				gap: 15px;
			}
		}
		&__item {
			width: calc((100% - 30px) / 2);

			@include mq(sm2) {
				width: calc((100% - 15px) / 2);
			}

			a {
				display: flex;
				justify-content: center;
				align-content: center;
				text-align: center;
				padding: 10px;
				border: 1px solid #fff;
				border-radius: 5px;

				@include mq(sm2) {
					line-height: 1.7;
				}

				&.active {
					background: #fff;
					color: $green;
					position: relative;
					&:after {
						content: "";
						width: 0px;
						height: 0px;
						border-style: solid;
						border-width: 12px 9.5px 0 9.5px;
						border-color: #fff transparent transparent transparent;
						transform: rotate(0deg);
						position: absolute;
						left: 50%;
						bottom: -20px;
						transform: translate(-50%, 0);
					}
				}
			}
		}
	}

	form {
		line-height: 1.8;
		table {
			width: 100%;
			display: block;
			margin-bottom: 0;

			tbody,
			thead,
			tr,
			th,
			td {
				display: block;
				width: 100%;
			}

			th,
			td {
				text-align: left;
			}

			th {
				font-weight: normal;
				position: relative;
				padding-bottom: 17px;
				font-weight: bold;
				@include mq(sm2) {
					padding-bottom: 15px;
					padding-left: 0;
				}
				.required,
				.optional {
					color: #fff;
					background: #db0000;
					font-size: 13px;
					padding: 1px 8px 2px;
					margin-right: 10px;
					border-radius: 100vh;
					@include mq(sm2) {
						position: static;
					}
				}
				.optional {
					background: #919191;
				}
			}

			td {
				padding-bottom: 40px;
				@include mq(sm2) {
					padding-bottom: 30px;
				}

				input[type="text"],
				input[type="email"],
				input[type="tel"],
				textarea {
					background: #fff;
					color: #000;
					width: 100%;
					padding: 14px;
					border-radius: 5px;
				}
				input.hasDatepicker {
					width: auto;
					max-width: 100%;
					background-color: #fff;
					background-image: url(../images/form/calendar.svg);
					background-repeat: no-repeat;
					background-position: right 15px center;
					background-size: 13px auto;
				}
				select {
					width: 100%;
					padding: 14px;
					padding-right: 30px;
					border-radius: 5px;
					background-color: #fff;
					color: #000;
					border: none;
				}
				::-webkit-input-placeholder {
					color: #ccc;
				}
				:-ms-input-placeholder {
					color: #ccc !important;
				}
				::-moz-placeholder {
					color: #ccc;
					opacity: 1;
				}
				::placeholder {
					color: #ccc;
					opacity: 1;
				}

				input[type="radio"] {
					display: none;

					+ .mwform-radio-field-text {
						position: relative;
						padding-left: 34px;

						&::before {
							position: absolute;
							content: "";
							display: block;
							width: 22px;
							height: 22px;
							border-radius: 50%;
							background: #fff;
							border: 1px solid $font-color;
							left: 0;
							top: 0;
						}

						&::after {
							position: absolute;
							content: "";
							display: block;
							width: 10px;
							height: 10px;
							border-radius: 50%;
							background: $red;
							left: 6px;
							top: 6px;
							opacity: 0;
							-webkit-transition: 0.3s;
							transition: 0.3s;
						}
					}

					&:checked + .mwform-radio-field-text::after {
						opacity: 1;
					}
				}

				textarea {
					height: 250px;
				}

				.zip-wrap,
				.zip-btn {
					width: calc(50% - 10px);
					border-radius: 0;

					@include mq(sm2) {
						width: calc(50% - 4px);
					}
				}
				.zip-wrap {
					@include mq(sm2) {
						width: 40%;
					}
				}
				.zip-btn {
					background: $blue;
					color: #fff;
					display: flex;
					align-items: center;
					justify-content: center;
					text-align: center;
					border-radius: 5px;
					transition: $transition;

					@include mq(sm2) {
						font-size: 12px;
						width: calc(60% - 10px);
					}

					input {
						@include mq(sm2) {
							width: 100%;
						}
					}

					&:hover {
						opacity: 0.5;
					}
				}

				.upload_file {
					&-wrap {
						width: 100%;
						padding: 15px;
						border-radius: 0;
						background: #fff;
						border-radius: 5px;
					}
					&__note {
						padding-top: 10px;
						font-size: 14px;
					}
				}

				.td-ttl {
					font-weight: 700;
					padding: 0 0 5px;
				}
				.flex-birth + .td-ttl {
					padding-top: 20px;
				}

				.flex-birth {
					display: flex;

					.birth-year-wrap,
					.birth-month-wrap,
					.birth-day-wrap {
						display: flex;
						align-items: flex-end;

						select {
							background-position: right 5px center;
						}
					}

					.birth-year-wrap,
					.birth-month-wrap {
						margin-right: 20px;
						position: relative;

						&.slash {
							&:after {
								content: "";
								display: block;
								height: 80%;
								width: 1px;
								background: #999;
								position: absolute;
								right: 0px;
								top: 50%;
								transform: translate(0, -50%) rotate(30deg);
							}
						}
					}
					.birth-month-wrap.no-slash {
						margin-right: 0px;

						&:after {
							display: none;
						}
					}

					.birth-year-wrap {
						width: 152px;

						select {
							width: 126px;

							@include mq(sm2) {
								width: 90px;
							}
						}
					}

					.birth-month-wrap,
					.birth-day-wrap {
						width: 108px;

						select {
							width: 82px;

							@include mq(sm2) {
								width: 64px;
							}
						}
					}
				}
			}

			tr.radio-tr {
				th {
					border-bottom: 1px solid $font-color;
				}

				td {
					padding-top: 10px;
					padding-bottom: 25px;
				}
			}

			tr.gender-tr {
				td {
					.mwform-radio-field {
						display: inline-block;
						margin: 0 15px 0 0;

						@include mq(sm2) {
							margin-right: 5px;
						}
					}
					label {
						border-radius: 5px;
						display: block;
						overflow: hidden;

						input[type="radio"] {
							display: none;

							+ .mwform-radio-field-text {
								position: relative;
								padding-left: 0;
								padding: 14px 20px;
								display: block;
								background: #cecece;
								color: #000;
								z-index: 1;

								@include mq(sm2) {
									padding: 14px 4vw;
								}

								&::before {
									display: none;
								}

								&::after {
									position: absolute;
									content: "";
									display: block;
									width: 100%;
									height: 100%;
									border-radius: 0;
									background: $orange;
									left: 0;
									top: 0;
									opacity: 0;
									-webkit-transition: 0.3s;
									transition: 0.3s;
									z-index: -1;
								}
							}

							&:checked {
								+ .mwform-radio-field-text {
									color: #fff;
									&::after {
										opacity: 1;
									}
								}
							}
						}
					}
				}
			}
			tr.c2 {
				td {
					display: flex;
					justify-content: space-between;

					.input-wrap {
						width: calc(50% - 10px);
					}
				}
			}
		}

		.input-wrap {
			position: relative;

			.error {
				white-space: nowrap;
			}
		}

		.agree {
			margin: 30px 0;
			display: flex;
			justify-content: center;
			text-align: center;

			@include mq(sm2) {
				margin: 0;
			}

			.input-wrap {
				.error {
					position: absolute;
					left: 0;
					bottom: -30px;
				}
			}

			a {
				font-weight: bold;
				border-bottom: 1px solid $orange;
				padding-bottom: 5px;
				color: $orange;
				&:hover {
					border-bottom: none;
				}
			}

			.agree-check {
				margin: 0 8px 5px 0;
				transform: scale(1.3);
				+ .mwform-checkbox-field-text {
					display: none;
				}
			}
		}

		.button-wrap {
			display: flex;
			flex-direction: column;
			justify-content: center;
			margin: 60px 0 40px;
			gap: 32px;
		}
		.submit,
		.back {
			display: flex;
			justify-content: center;
		}
		.input-btn {
			background-color: $orange;
			background-image: url(../images/common/arrow-right-white-3.svg);
			background-repeat: no-repeat;
			background-position: right 20px center;
			background-size: 20px auto;
			color: #fff;
			position: relative;
			text-align: center;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 370px;
			height: 60px;
			border-radius: 5px;
			transition: $transition;
			@include mq(sm2) {
				width: 100%;
			}

			&.back {
				margin-top: 10px;
				background-color: transparent;
				background-image: url(../images/common/arrow-right-white-3.svg);
				padding: 0 50px 0 25px;
				color: $font-color;
				width: auto;
				height: auto;
				&:hover {
					opacity: 0.5;
				}
			}

			&:hover {
				opacity: 0.5;
			}
		}
		.error {
			font-size: 12px;
			line-height: 1.4;
			margin-top: 5px;
		}
	}

	&.confirm {
		padding-top: 80px;

		@include mq(sm2) {
			padding-top: 50px;
		}

		.entry-form-inner {
			max-width: 720px;
		}
		.entry-form__lead {
			margin-bottom: 0;
		}
		form {
			table {
				border-top: 1px solid #fff;
				th {
					padding-top: 30px;
					padding-bottom: 10px;
					.required,
					.optional {
						display: none;
					}
				}
				td {
					border-bottom: 1px solid #fff;
					padding-bottom: 30px;

					.zip-btn {
						display: none;
					}
					.upload_file-wrap {
						width: 100%;
						padding: 0;
						border: none;
						background: none;
					}
					.upload_file__note {
						display: none;
					}

					.flex-birth {
						.birth-year-wrap,
						.birth-month-wrap {
							width: auto;

							&:after {
								height: 15px;
								right: -9px;
								background: #fff;
							}
						}
					}
				}
				tr.c2 {
					td {
						justify-content: flex-start;
						.input-wrap {
							width: auto;
						}
					}
				}
			}
			.agree {
				display: none;
			}
		}
	}
}
.mw_wp_form .error {
	color: #db0000 !important;
}
