@charset "UTF-8";

.about-philosophy {
	padding: 120px 0;

	@include mq(sm2) {
		padding: 70px $sp-padding 0;
	}

	&__body {
		font-size: 18px;
		text-align: center;
		line-height: 2.5;

		@include mq(sm2) {
			font-size: vw2(15);
		}
	}
}
.about-vision {
	margin: 120px 0;

	&__list {
		display: flex;
		gap: 100px;

		@include mq(sm2) {
			flex-direction: column;
			gap: 50px;
			padding: 0 $sp-padding;
		}
	}
	&__item {
		width: calc((100% - 200px) / 3);
		position: relative;

		@include mq(sm2) {
			width: 100%;
		}

		&:after {
			content: "";
			width: calc(100% + 100px - 75px);
			height: 1px;
			background: #fff;
			position: absolute;
			left: calc(50% + 38px);
			top: 37px;

			@include mq(sm2) {
				display: none;
			}
		}
		&:last-child {
			&:after {
				display: none;
			}
		}

		.badge {
			width: 75px;
			height: 75px;
			border-radius: 50%;
			border: 1px solid #fff;
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			font-family: $font-a;
			margin: 0 auto 25px;
		}
		.ttl {
			text-align: center;
			font-family: $font-a;
			font-size: 20px;
		}
	}
}

.about-mp {
	padding: 140px 0;
	position: relative;
	overflow: hidden;

	@include mq(sm2) {
		padding: 90px $sp-padding;
	}

	&:before {
		content: "";
		display: block;
		width: vw(3990);
		height: vw(5990);
		border-radius: 50%;
		background-color: rgba(255, 255, 255, 0.5);
		position: absolute;
		left: 50%;
		top: 0;
		transform: translate(-50%, 0);
		backdrop-filter: blur(30px);

		@include mq(sm2) {
			width: vw2(2300);
			height: vw2(3510);
		}
	}

	&-inner {
		position: relative;
	}
	.c-ttl {
		.en {
			@include mq(sm2) {
				img {
					height: vw2(85);
				}
			}
		}
	}
	&__ttl {
		background: $green;
		border-radius: 10px;
		text-align: center;
		font-family: $font-a;
		font-size: 20px;
		line-height: 1.5;
		font-weight: 400;
		padding: 15px;

		@include mq(sm2) {
			font-size: vw2(15);
			margin-bottom: 25px;
		}
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 20px;
	}
	&__item {
		width: calc((100% - 40px) / 3);
		position: relative;
		overflow: hidden;
		border-radius: 10px;

		@include mq(sm2) {
			width: 100%;
		}

		&:before {
			@include mq(sm2) {
				content: "●Tap";
				position: absolute;
				right: 13px;
				bottom: 3px;
				text-align: right;
				color: $green;
				font-size: vw2(18);
				z-index: 1;
				font-weight: bold;
			}
		}

		&:after {
			content: "";
			display: block;
			width: 60px;
			height: 60px;
			background: $orange;
			position: absolute;
			right: -30px;
			bottom: -30px;
			transform: rotate(45deg);
		}
		.front {
			text-align: center;
			background: #fff;
			padding: 30px 30px 45px;
			border-radius: 10px;
			width: 100%;
			height: 100%;
			.icon {
				margin-bottom: 20px;
			}
			.txt {
				color: $green;
				font-family: $font-a;
				font-size: 20px;
				line-height: 1.5;
				font-weight: 500;
				@include mq(sm2) {
					font-size: vw2(19);
				}
			}
		}
		.back {
			opacity: 0;
			position: absolute;
			left: 0;
			top: 0;
			background: #659990;
			width: 100%;
			height: 100%;
			transition: $transition;
			padding: 50px 30px;
		}
		&:hover {
			.back {
				opacity: 1;
			}
		}
	}
}
.about-cn {
	padding: 120px 0;
	position: relative;

	@include mq(sm2) {
		padding: 90px 0 30px;
	}

	&:before {
		content: "";
		display: block;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 50, 42, 0.5);
		position: absolute;
		left: 0;
		top: 0;
		backdrop-filter: blur(30px);
		border-radius: 10px;
		mix-blend-mode: multiply;

		@include mq(sm2) {
			border-radius: 0;
		}
	}

	&-inner {
		position: relative;
	}
	&__list {
		display: flex;

		@include mq(sm2) {
			display: block;
			padding: 0 $sp-padding;
		}
	}
	&__item {
		width: 33.33%;
		text-align: center;
		position: relative;
		padding: 50px 0;

		@include mq(sm2) {
			width: 100%;
		}

		&:before,
		&:after {
			content: "";
			width: 1px;
			height: 100%;
			position: absolute;
			left: 0;
			top: 50%;
			transform: translate(0, -50%);
			/* 線の長さ2pxの線を背景画像とする */
			background-image: linear-gradient(to bottom, #fff 4px, transparent 4px);
			/* 背景画像の大きさを設定 */
			background-size: 1px 8px;
			/* 背景の開始位置を指定 */
			background-position: left top;
			/* 横方向に繰り返す */
			background-repeat: repeat-y;

			@include mq(sm2) {
				width: 100%;
				height: 1px;
				top: auto;
				bottom: 0;
				transform: translate(0, 0);
				/* 線の長さ2pxの線を背景画像とする */
				background-image: linear-gradient(to right, #fff 4px, transparent 4px);
				/* 背景画像の大きさを設定 */
				background-size: 8px 1px;
				/* 背景の開始位置を指定 */
				background-position: left bottom;
				/* 横方向に繰り返す */
				background-repeat: repeat-x;
			}
		}
		&:after {
			left: auto;
			right: 0;
			display: none;

			@include mq(sm2) {
				display: none;
			}
		}
		&:last-child {
			&:after {
				display: block;

				@include mq(sm2) {
					display: none;
				}
			}
			&:before {
				@include mq(sm2) {
					display: none;
				}
			}
		}

		.initial {
			margin-bottom: 10px;
		}
		.meaning {
			margin-bottom: 20px;
		}
		.txt {
		}
	}
}

.about-message {
	margin: 120px 0;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&-inner {
		background: url(../images/about/message-bg.png) no-repeat center / cover;
		aspect-ratio: 1088 / 521;
		height: auto;
		padding: 50px 60px;
		border-radius: 10px;
		overflow: hidden;

		@include mq(sm2) {
			background: none;
			padding: 0;
			overflow: visible;
		}
	}
	.c-ttl {
		margin-bottom: 13px;

		&-inner {
			@include mq(sm2) {
				background: url(../images/about/sp/message-bg.png) no-repeat center / cover;
				aspect-ratio: 341 / 201;
				width: 100%;
				height: auto;
				border-radius: 10px;
				overflow: hidden;
				display: flex;
				flex-direction: column;
				justify-content: center;
				padding: 0 0 0 20px;
				margin-bottom: 30px;
			}
		}
	}
	&__body {
		max-width: 488px;
	}
	&__name {
		font-size: 18px;
		font-weight: 500;
		margin-top: 20px;
	}
}

.about-ci {
	margin: 120px 0;
	color: $green;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&-inner {
		position: relative;
		padding: 80px 90px;

		@include mq(sm2) {
			padding: 50px 20px;
		}

		&:before {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			background-color: rgba(255, 255, 255, 0.8);
			position: absolute;
			left: 0;
			top: 0;
			backdrop-filter: blur(30px);
			border-radius: 10px;
		}
	}
	&-inner-2 {
		position: relative;
	}

	dl {
		display: flex;
		flex-wrap: wrap;
		border-top: 1px solid $green;

		dt,
		dd {
			border-bottom: 1px solid $green;
			display: flex;
			justify-content: center;
			flex-direction: column;
			padding: 20px;

			@include mq(sm2) {
				padding: 0;
			}
		}
		dt {
			width: 220px;
			@include mq(sm2) {
				width: 100%;
				border-bottom: none;
				padding-top: 15px;
			}
		}
		dd {
			width: calc(100% - 220px);
			@include mq(sm2) {
				width: 100%;
				padding-bottom: 15px;
			}
		}
	}
}

.about-history {
	margin: 120px 0;
	padding-bottom: 12vw;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding 12vw;
	}

	&__list {
		position: relative;
		&:before {
			content: "";
			width: 1px;
			height: 100%;
			display: block;
			background: #d2c79c;
			position: absolute;
			left: 8px;
			top: 7px;
		}
	}
	&__item {
		padding: 0 0 50px;

		&:last-child {
			padding: 0;
		}
		.date {
			color: #d2c79c;
			font-weight: bold;
			position: relative;
			padding-left: 35px;
			&:before {
				content: "";
				width: 18px;
				height: 18px;
				display: block;
				border-radius: 50%;
				background: #d2c79c;
				position: absolute;
				left: 0;
				top: 7px;
			}
		}
		.ttl {
			font-weight: bold;
			padding-left: 35px;
			margin-bottom: 0;
		}
		.body {
			padding-left: 35px;
		}
	}
}
