@charset "UTF-8";

.pp-sect {
	margin: 120px 0 0;
	padding-top: 120px;
	padding-bottom: calc(12vw + 120px);

	background: #2b5d55;
	background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 40px 0 80px;
		padding: 50px $sp-padding 12vw;
	}

	&-unit {
		border-bottom: 1px solid #fff;
		padding: 40px 0;

		@include mq(sm2) {
			padding: 30px 0;
		}

		&:first-child {
			padding-top: 0;
		}

		h2 {
			font-size: 18px;
			font-weight: bold;

			@include mq(sm2) {
				font-size: vw2(17);
			}
		}
		.body {
			@include mq(sm2) {
				font-size: vw2(14);
			}

			a {
				border-bottom: 1px solid #fff;
			}
		}
	}
}
