@charset "UTF-8";

.archive-interview {
	margin: 120px 0 0;
	padding-top: 120px;
	padding-bottom: calc(12vw + 120px);

	background: #2b5d55;
	background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 80px 0;
		padding: 50px $sp-padding 12vw;
	}

	&-nav {
		margin-bottom: 50px;
		&__list {
			display: flex;
			flex-wrap: wrap;
			gap: 20px;

			@include mq(sm2) {
				gap: 15px;
			}
		}
		&__item {
			width: calc((100% - 60px) / 4);

			@include mq(sm2) {
				width: calc((100% - 15px) / 2);
			}

			a {
				display: flex;
				justify-content: center;
				align-content: center;
				text-align: center;
				padding: 10px;
				border: 1px solid #fff;
				border-radius: 5px;

				&.active {
					background: #fff;
					color: $green;
				}
			}
		}
	}
	&-header {
		display: flex;
		justify-content: space-between;
		font-family: $font-a;
		font-size: 20px;
		margin-bottom: 25px;

		@include mq(sm2) {
			margin-bottom: 20px;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 50px;

		@include mq(sm2) {
			gap: 30px;
		}
	}
	.index-interview__item {
		width: calc((100% - 100px) / 3);
		margin: 0;

		@include mq(sm2) {
			width: 100%;
		}
	}
}

.single-interview {
	padding-top: 120px;

	@include mq(sm2) {
		padding-top: 80px;
	}

	&-inner {
		padding-left: vw(140);
		display: grid;
		grid-template-columns: 45% 55%;
		grid-template-rows: auto 1fr;
		grid-column-gap: 0px;
		grid-row-gap: 0px;

		@include mq(sm2) {
			display: block;
			padding: 0 0 0 $sp-padding;
		}
	}

	&-header {
		display: grid;
		grid-template-columns: repeat(2, auto) 1fr;
		grid-template-rows: auto 1fr;
		grid-column-gap: 0px;
		grid-row-gap: 25px;
		margin-bottom: 80px;

		@include mq(sm2) {
			display: grid;
			grid-template-columns: auto 1fr;
			grid-template-rows: repeat(2, auto) 1fr;
			grid-column-gap: 0px;
			grid-row-gap: 7px;
			margin-bottom: 20px;
			padding-right: $sp-padding;
		}

		.occupation {
			order: 2;
			margin-right: 20px;
			align-self: center;

			@include mq(sm2) {
				grid-area: 1 / 1 / 2 / 3;
				margin-bottom: 10px;
			}

			span {
				border: 1px solid #fff;
				border-radius: 100vh;
				padding: 5px 18px;
			}
		}
		.name {
			order: 1;
			font-family: $font-a;
			font-size: 30px;
			line-height: 1.33;
			margin-right: 30px;
			position: relative;

			@include mq(sm2) {
				font-size: vw2(20);
				margin-right: 20px;
			}

			&:after {
				content: "";
				display: block;
				width: 1px;
				height: 100%;
				background: #fff;
				position: absolute;
				right: -15px;
				top: 50%;
				transform: translate(0, -50%);

				@include mq(sm2) {
					display: none;
				}
			}
		}
		.data {
			order: 3;
			align-self: center;

			@include mq(sm2) {
				position: relative;
				top: -2px;
			}
		}
		.message {
			order: 4;
			grid-area: 2 / 1 / 3 / 4;
			font-family: $font-a;
			font-size: 30px;
			line-height: 1.33;
			font-weight: 500;

			@include mq(sm2) {
				grid-area: 3 / 1 / 4 / 3;
				font-size: vw2(20);
				line-height: 1.75;
			}
		}
	}
	&-img {
		grid-area: 1 / 2 / 3 / 3;

		@include mq(sm2) {
			margin-bottom: 20px;
		}
		img {
			border-radius: 10px 0 0 10px;
			width: 100%;
			position: sticky;
			top: 120px;
			right: 0;
		}
	}
	&-interview {
		padding-right: 50px;

		@include mq(sm2) {
			padding-right: $sp-padding;
		}

		&-unit {
			margin-bottom: 80px;
			.question {
				font-family: $font-a;
				font-size: 20px;
				line-height: 1.6;
				font-weight: 500;
				border-bottom: 1px solid #fff;
				padding: 0 0 10px;
				margin-bottom: 16px;
				.bullet {
				}
			}
			.answer {
			}
		}
	}
}
