@charset "UTF-8";

.job-wrap {
	position: relative;
	padding-bottom: 12vw;
	&:before {
		content: "";
		display: block;
		width: 100%;
		height: calc(100% + 340px);
		background-color: rgba(0, 50, 42, 0.5);
		position: absolute;
		left: 0;
		bottom: 0;
		backdrop-filter: blur(30px);
		mix-blend-mode: multiply;

		@include mq(sm2) {
			height: calc(100% + 150px);
		}
	}

	&-inner {
		position: relative;
	}
}

.job-strength {
	margin: 120px 0;

	@include mq(sm2) {
		margin: 50px 0 80px;
		padding: 0 $sp-padding;
	}

	&__list {
		display: flex;
		gap: 40px;

		@include mq(sm2) {
			flex-direction: column;
		}
	}
	&__item {
		background: #fff;
		border-radius: 10px;
		width: calc((100% - 80px) / 3);
		color: $green;
		padding: 45px 20px;

		@include mq(sm2) {
			width: 100%;
		}

		.point {
			text-align: center;
			margin-bottom: 10px;
		}
		.ttl {
			text-align: center;
			font-family: $font-a;
			font-size: 20px;
		}
	}
}

.job-occupation {
	margin: 120px 0;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&__list {
	}
	&__item {
		border: 1px solid #fff;
		border-radius: 10px;
		padding: 30px 25px;
		margin-bottom: 50px;
		position: relative;

		@include mq(sm2) {
			padding: 30px 20px;
			margin-bottom: 30px;
		}

		&:before {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			background-color: rgba(255, 255, 255, 0.2);
			position: absolute;
			left: 0;
			top: 0;
			backdrop-filter: blur(30px);
			border-radius: 10px;
		}
		&-inner {
			position: relative;
		}
		.flex {
			display: grid;
			grid-template-columns: 60% 40%;
			grid-template-rows: auto 1fr;
			grid-column-gap: 0px;
			grid-row-gap: 0px;
			border-bottom: 1px solid #fff;
			padding-bottom: 30px;
			margin-bottom: 20px;

			@include mq(sm2) {
				display: flex;
				flex-direction: column;
			}
			.ttl {
				padding-top: 30px;
				@include mq(sm2) {
					padding-top: 0;
					order: 0;
					font-size: vw2(20);
				}
			}
			.img {
				grid-area: 1 / 2 / 3 / 3;

				@include mq(sm2) {
					order: 1;
					margin-bottom: 15px;
				}

				img {
					border-radius: 10px;
				}
			}
			.body {
				padding-right: 70px;

				@include mq(sm2) {
					order: 2;
					padding: 0;
				}
			}
		}
		.ttl {
			font-family: $font-a;
			font-size: 20px;
			margin-bottom: 12px;

			@include mq(sm2) {
				font-size: vw2(15);
			}

			small {
				font-size: 15px;

				@include mq(sm2) {
					font-size: vw2(13);
				}
			}
		}
		.job-list {
			display: flex;
			flex-wrap: wrap;
			gap: 20px;

			@include mq(sm2) {
				gap: 15px;
			}

			> li {
				background: #fff;
				color: $green;
				width: calc((100% - 20px) / 2);
				border-radius: 10px;
				text-align: center;
				padding: 15px 10px;

				@include mq(sm2) {
					width: 100%;
				}

				&.div3 {
					width: calc((100% - 40px) / 3);

					@include mq(sm2) {
						width: 100%;
					}
				}
			}
		}

		.schedule {
			margin-top: 60px;
			&__ttl {
				margin-bottom: 20px;
				span {
					display: block;
					font-family: $font-a;
					line-height: 1.7;
					&.line-1 {
						font-size: 20px;
					}
					&.line-2 {
						font-size: 15px;
					}
				}
			}
			&__list-wrap {
				background: #fff;
				padding: 20px 0px 20px 40px;
				border-radius: 10px;
				color: $green;

				@include mq(sm2) {
					padding: 20px 0px 20px 20px;
				}
			}
			&__list {
				max-height: 600px;
				overflow: auto;
				padding-right: 40px;

				@include mq(sm2) {
					padding-right: 20px;
				}
			}
			&__item {
				border-bottom: 1px solid $green;
				padding: 30px 0;

				&:last-child {
					border-bottom: none;
				}

				.header {
					display: flex;
					align-items: flex-start;
					gap: 10px;
					line-height: 1.67;

					@include mq(sm2) {
						flex-direction: column;
						gap: 10px;
					}

					.time {
						background: $orange;
						color: #fff;
						font-size: 15px;
						font-weight: bold;
						padding: 0 10px;
						border-radius: 100vh;
						flex: 0 0 68px;
						text-align: center;

						@include mq(sm2) {
							flex: 0 0 auto;
							font-size: vw2(15);
						}
					}
					.ttl {
						font-size: 20px;
						font-weight: bold;
						margin: 0;
						font-family: $font-base;
						position: relative;
						top: -5px;

						@include mq(sm2) {
							font-size: vw2(18);
							top: 0px;
							line-height: 1.5;
							margin-bottom: 10px;
						}
					}
				}
				.cnt {
					line-height: 1.67;
					font-weight: 500;

					ul {
						li {
							padding-left: 13px;
							position: relative;
							&:before {
								content: "・";
								position: absolute;
								left: 0;
								top: 0;
							}
						}
					}
				}
			}
		}
	}
}

.job-careerpath {
	margin: 120px 0;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	.tab_box {
		.btn_area {
			display: flex;
			justify-content: center;

			.tab_btn {
				width: calc(100% / 5);
				height: 72px;
				background: #f5f7f8;
				text-align: center;
				cursor: pointer;
				transition: all 0.2s ease 0s;
				border-radius: 10px 10px 0 0;
				border: 1px solid #fff;

				background-color: rgba(255, 255, 255, 0.2);
				backdrop-filter: blur(30px);
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				font-family: $font-a;
				line-height: 1.5;

				@include mq(sm2) {
					display: none;
				}

				.line-1 {
					font-size: 20px;
					font-weight: 600;
				}
				.line-2 {
					font-size: 15px;
					font-weight: 600;
				}

				&:hover {
					opacity: 0.5;
				}
				&.active {
					background: #fff;
					color: $green;
				}
			}
		}
		.panel_area {
			background: #fff;
			color: $green;
			padding: 40px;
			border-radius: 0 0 10px 10px;

			@include mq(sm2) {
				padding: 0px;
				display: flex;
				flex-direction: column;
				gap: 17px;
				background: none;
			}

			.tab_panel {
				display: none;

				@include mq(sm2) {
					display: block;
					background: #fff;
					border-radius: 10px;
				}

				&.active {
					display: block;
				}

				.header {
					display: none;

					@include mq(sm2) {
						display: block;
						padding: 15px 20px;
						position: relative;

						&:before {
							content: "";
							width: 0px;
							height: 0px;
							border-style: solid;
							border-width: 10px 6px 0 6px;
							border-color: $orange transparent transparent transparent;
							transform: rotate(0deg);
							position: absolute;
							right: 20px;
							top: 50%;
							transform: translate(0, -50%);
							transition: $transition;
						}

						&.open {
							&:before {
								transform: translate(0, -50%) scaleY(-1);
							}
						}

						.line-1 {
							font-size: vw2(20);
						}
						.line-2 {
							font-size: vw2(15);
						}
					}
				}
				.cnt {
					@include mq(sm2) {
						display: none;
						padding: 20px;
						padding-top: 0;
					}
					.ttl {
						font-family: $font-a;
						font-size: 20px;
						font-weight: 600;
						border-bottom: 1px solid $green;
						margin-bottom: 20px;
						padding-bottom: 10px;

						@include mq(sm2) {
							font-size: vw2(19);
							line-height: 1.67;
						}
					}
					ul {
						display: flex;
						flex-wrap: wrap;
						gap: 20px;

						@include mq(sm2) {
							flex-direction: column;
							gap: 10px;
						}

						li {
							width: calc((100% - 40px) / 3);
							background: $green;
							color: #fff;
							text-align: center;
							border-radius: 5px;
							padding: 20px;
							line-height: 1.67;

							@include mq(sm2) {
								width: 100%;
								font-size: 13px;
								padding: 13px;
							}
						}
					}
				}
			}
		}
	}
}

.job-schedule {
	margin: 120px 0;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 32px;

		@include mq(sm2) {
			gap: 20px;
		}

		&.col-3 {
			gap: 40px;

			@include mq(sm2) {
				gap: 20px;
			}

			.job-schedule__item {
				width: calc((100% - 80px) / 3);

				@include mq(sm2) {
					width: 100%;
				}

				&:nth-child(4n) {
					&:after {
						display: block;

						@include mq(sm2) {
							display: none;
						}
					}
				}
				&:nth-child(3n) {
					&:after {
						display: none;
					}
				}
			}
		}
	}
	&__item {
		width: calc((100% - 96px) / 4);
		border: 1px solid #fff;
		border-radius: 10px;
		position: relative;
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
		min-height: 100px;

		@include mq(sm2) {
			width: 100%;
			justify-content: flex-start;
			min-height: 50px;
			gap: 15px;
		}

		&:before {
			content: "";
			display: block;
			width: 100%;
			height: 100%;
			background-color: rgba(255, 255, 255, 0.2);
			position: absolute;
			left: 0;
			top: 0;
			backdrop-filter: blur(30px);
			border-radius: 10px;
		}
		&:after {
			content: "";
			width: 0px;
			height: 0px;
			border-style: solid;
			border-width: 25px 0 25px 13px;
			border-color: transparent transparent transparent #fff;
			transform: rotate(0deg);
			position: absolute;
			right: -26px;
			top: 50%;
			transform: translate(0, -50%);

			@include mq(sm2) {
				display: none;
			}
		}
		&:nth-child(4n) {
			&:after {
				display: none;
			}
		}
		.time {
			position: absolute;
			left: 0;
			top: 0;
			color: $orange;
			background: #fff;
			border-radius: 10px 0 5px 0;
			line-height: 1.6;
			padding: 0 7px;

			@include mq(sm2) {
				position: relative;
				border-radius: 10px 0 0 10px;
				height: 100%;
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
		.ttl {
			font-size: 20px;
			font-weight: bold;
			position: relative;
			line-height: 1.4;

			@include mq(sm2) {
				font-size: 15px;
				text-align: left;
				padding: 10px 10px 10px 0;
			}

			small {
				font-size: 15px;
			}
		}
	}
}

.job-icp {
	margin: 160px 0 120px;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&__illust {
		position: relative;
		text-align: center;
		width: 903px;
		height: 867px;
		position: relative;
		margin: 0 auto;
		margin-top: -200px;

		@include mq(sm2) {
			margin-top: 0;
			width: 100%;
			height: auto;
			margin-top: 0;
		}

		&__ttl {
			width: 234px;
			height: 234px;
			position: absolute;
			left: 50%;
			top: calc(50% + 40px);
			transform: translate(-50%, -50%);

			@include mq(sm2) {
				display: none;
			}

			&-inner {
				width: 100%;
				height: 100%;
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;

				&:before {
					content: "";
					display: block;
					width: 100%;
					height: 100%;
					position: absolute;
					left: 0;
					top: 0;
					background: url(../images/job/visiting/icp-circle.svg) no-repeat center / contain;
					animation: 3s linear infinite rotation1;
				}
			}

			span {
				display: block;
				font-family: $font-a;
				line-height: 1.5;

				&.line-1 {
					font-size: 30px;
				}
				&.line-2 {
					font-size: 20px;
				}
			}
		}

		.circle-1,
		.circle-2 {
			border: 1px solid #fff;
			border-radius: 50%;
			position: absolute;
			left: 50%;
			top: calc(50% + 40px);
			transform: translate(-50%, -50%);
			z-index: 0;
			opacity: 0;

			@include mq(sm2) {
				display: none;
			}
		}
		.circle-1 {
			width: 740px;
			height: 740px;
		}
		.circle-2 {
			width: 560px;
			height: 560px;
		}

		&__list {
			width: 100%;
			height: 100%;
			position: relative;
			z-index: 1;

			.slick-prev,
			.slick-next {
				@include mq(sm2) {
					width: 12px;
					height: 24px;
				}
			}
			.slick-prev {
				@include mq(sm2) {
					left: -5px;
				}
				&:before {
					background-image: url(../images/slick/left-2.svg);
				}
			}
			.slick-next {
				@include mq(sm2) {
					right: -5px;
				}
				&:before {
					background-image: url(../images/slick/right-2.svg);
				}
			}
		}
		&__item {
			background-color: rgba(255, 255, 255, 0.2);
			width: 274px;
			height: 274px;
			border-radius: 50%;
			backdrop-filter: blur(30px);
			border: 1px solid #fff;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			gap: 15px;
			position: absolute;

			@include mq(sm2) {
				position: static;
				display: flex !important;
				height: auto;
				aspect-ratio: 1/1;
				margin: 0 20px;
			}

			&.item-1 {
				left: 50%;
				top: 0;
				transform: translate(-50%, 0);

				@include mq(sm2) {
					transform: translate(0, 0);
				}
			}
			&.item-2 {
				right: 0;
				top: 212px;
			}
			&.item-3 {
				right: 88px;
				bottom: 0;
			}
			&.item-4 {
				left: 88px;
				bottom: 0;
			}
			&.item-5 {
				left: 0;
				top: 212px;
			}
			.icon {
				img {
					&.w-70 {
					}
					&.w-73 {
					}
				}
			}
			.txt {
				line-height: 1.8;
			}
		}

		img {
			&.circle {
				width: 234px;
				position: absolute;
				top: 372px;
				left: 429px;
				animation: 3s linear infinite rotation1;

				@include mq(sm2) {
					width: 100%;
					top: 50%;
					left: 50%;
					width: 30px;
				}
			}
		}
	}
	&.in {
		.circle-1,
		.circle-2 {
			animation: circleAppearance 1s 1s forwards;
		}
	}
}

.job-icskill {
	margin: 160px 0 120px;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&-inner {
		display: flex;

		@include mq(sm2) {
			display: block;
		}
	}

	&__illust {
		position: relative;
		text-align: center;
		width: 474px;
		height: 460px;
		position: relative;

		@include mq(sm2) {
			margin-top: 0;
			width: 100%;
			height: auto;
			margin-top: 0;
			margin-bottom: 30px;
			aspect-ratio: 474 / 460;
		}

		&__ttl {
			width: 210px;
			height: 210px;
			position: absolute;
			left: 50%;
			top: calc(50% + 30px);
			transform: translate(-50%, -50%);
			font-family: $font-a;
			font-size: 30px;
			line-height: 1.2;
			display: flex;
			justify-content: center;
			align-items: center;

			@include mq(sm2) {
				font-size: vw2(18);
				top: calc(50% + 3vw);
				line-height: 1.75;
			}
		}

		.circle-1,
		.circle-2 {
			border: 1px solid #fff;
			border-radius: 50%;
			position: absolute;
			left: 50%;
			top: calc(50% + 30px);
			transform: translate(-50%, -50%);
			z-index: 0;
			opacity: 0;

			@include mq(sm2) {
				top: calc(50% + 3vw);
			}
		}
		.circle-1 {
			width: 416px;
			height: 416px;

			@include mq(sm2) {
				width: vw2(274);
				height: vw2(274);
			}
		}
		.circle-2 {
			width: 314px;
			height: 314px;

			@include mq(sm2) {
				width: vw2(206);
				height: vw2(206);
			}
		}

		&__list {
			width: 100%;
			height: 100%;
			position: relative;
			z-index: 1;
		}
		&__item {
			background-color: rgba(255, 255, 255, 0.2);
			width: 154px;
			height: 154px;
			border-radius: 50%;
			backdrop-filter: blur(30px);
			border: 1px solid #fff;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			gap: 15px;
			position: absolute;

			@include mq(sm2) {
				width: vw2(120);
				height: vw2(120);
			}

			&.item-1 {
				left: 50%;
				top: 0;
				transform: translate(-50%, 0);
			}
			&.item-2 {
				right: 0;
				bottom: 36px;
			}
			&.item-3 {
				left: 0;
				bottom: 36px;
			}

			.icon {
				@include mq(sm2) {
					img {
						max-width: 80%;
					}
				}
			}
		}
	}
	&__txt {
		width: calc(100% - 474px);
		padding-left: 32px;

		@include mq(sm2) {
			width: 100%;
			padding: 0;
		}

		.unit-wrap {
			display: flex;
			flex-direction: column;
			gap: 26px;
			.unit {
				background: #fff;
				border-radius: 10px;
				color: $green;
				padding: 20px 32px;

				@include mq(sm2) {
					text-align-last: center;
					padding: 20px 5px;
				}

				.ttl {
					font-family: $font-a;
					font-size: 20px;
					font-weight: 600;

					@include mq(sm2) {
						font-size: vw2(20);
						line-height: 1.5;
					}
				}
				.body {
					@include mq(sm2) {
						font-size: vw2(15);
					}
				}
			}
		}
	}
	&.in {
		.circle-1,
		.circle-2 {
			animation: circleAppearance 1s 1s forwards;
		}
	}
}

@keyframes circleAppearance {
	0% {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.job-benefit {
	margin: 160px 0 120px;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 18px;

		@include mq(sm2) {
			margin-bottom: 18px;
		}
	}
	&__item {
		width: calc((100% - 54px) / 4);
		background: #fff;
		border-radius: 10px;
		text-align: center;
		color: $green;
		padding: 30px 10px;

		@include mq(sm2) {
			width: calc((100% - 18px) / 2);
			padding: 20px 5px;
		}

		.icon {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 85px;
			margin-bottom: 10px;
		}
		.ttl {
			font-size: 20px;
			font-weight: bold;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			@include mq(sm2) {
				font-size: vw2(15);
			}
		}
	}
	.other {
		background: #fff;
		border-radius: 10px;
		color: $green;
		padding: 30px 10px;
		display: flex;
		gap: 50px;
		justify-content: center;

		@include mq(sm2) {
			flex-direction: column;
			gap: 0;
			padding: 23px 21px;
		}

		ul {
			margin: 0;
			li {
				font-weight: bold;
			}
		}
	}
}

.job-induction {
	margin: 140px 0 0;
	padding-bottom: 12vw;

	@include mq(sm2) {
		margin: 80px 0 0;
		padding: 0 $sp-padding 12vw;
	}

	&__lead {
		font-family: $font-a;
		font-size: 20px;
		border-bottom: 1px solid #fff;
		text-align: center;
		padding-bottom: 26px;
		margin-bottom: 50px;

		@include mq(sm2) {
			font-size: vw2(16);
		}
	}

	&__list {
		display: flex;
		gap: 30px;

		@include mq(sm2) {
			flex-direction: column;
			gap: 40px;
		}
	}
	&__item {
		width: calc((100% - 30px) / 2);
		text-align: center;

		@include mq(sm2) {
			width: 100%;
		}

		.ttl {
			font-family: $font-a;
			font-size: 30px;
			font-weight: 400;
			margin-bottom: 20px;

			@include mq(sm2) {
				font-size: vw2(20);
			}
		}
		.btn {
			a {
				display: flex;
				justify-content: center;
				align-items: center;
				color: #fff;
				background-color: #659990;
				border-radius: 100vh;
				height: 90px;
				font-size: 20px;
				font-weight: bold;
				gap: 10px;
				position: relative;

				@include mq(sm2) {
					font-size: vw2(18);
				}

				.arrow,
				.icon {
					display: flex;
					align-items: center;
				}
				.icon {
					img {
						width: 15px;
					}
				}
				.arrow {
					position: absolute;
					right: 30px;
					top: 50%;
					transform: translate(0, -50%);
					img {
						width: 14px;
					}
				}
			}
			&.orange {
				a {
					background: $orange;
				}
			}
		}
	}
}
