@charset "UTF-8";

.archive-recruitment {
	margin: 120px 0 0;
	padding-top: 120px;
	padding-bottom: calc(12vw + 120px);

	background: #2b5d55;
	background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 40px 0 80px;
		padding: 50px $sp-padding 12vw;
	}

	&-nav {
		margin-bottom: 50px;

		&__ttl {
			text-align: center;
			font-family: $font-a;
			font-size: 20px;
			margin-bottom: 25px;

			@include mq(sm2) {
				font-size: vw2(17);
				margin-bottom: 20px;
			}
		}
		&__list {
			display: flex;
			flex-wrap: wrap;
			gap: 30px;

			@include mq(sm2) {
				gap: 15px;
			}
		}
		&__item {
			width: calc((100% - 30px) / 2);

			@include mq(sm2) {
				width: calc((100% - 15px) / 2);
			}

			a {
				display: flex;
				justify-content: center;
				align-content: center;
				text-align: center;
				padding: 10px;
				border: 1px solid #fff;
				border-radius: 5px;

				&.active {
					background: #fff;
					color: $green;
				}
			}
		}
	}
	&-search {
		background-color: #fff;
		color: $green;
		border-radius: 10px;
		margin-bottom: 80px;
		&-header {
			padding: 20px 30px;
			position: relative;
			cursor: pointer;

			@include mq(sm2) {
				padding: 15px 20px;
			}

			.ttl {
				display: flex;
				align-items: center;
				gap: 10px;
				font-weight: bold;
				.icon {
					font-size: 20px;

					@include mq(sm2) {
						font-size: 18px;
					}
				}
			}
			.status {
				position: absolute;
				right: 30px;
				top: 50%;
				transform: translate(0, -50%);
				width: 17px;
				height: 17px;

				@include mq(sm2) {
					right: 20px;
				}

				&-inner {
					width: 100%;
					height: 100%;
					position: relative;
					.line-1,
					.line-2 {
						width: 100%;
						height: 3px;
						background: $orange;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
					}
					.line-2 {
						transform: translate(-50%, -50%) rotate(90deg);
						transition: $transition;
					}
				}
			}

			&.open {
				border-radius: 20px 20px 0 0;
				.status {
					.line-2 {
						transform: translate(-50%, -50%) rotate(360deg);
					}
				}
			}
		}
		&-cnt {
			padding: 0 30px;
			padding-bottom: 50px;
			display: none;

			@include mq(sm2) {
				padding: 0 20px 40px;
			}
		}
		&__dl {
			display: flex;
			flex-wrap: wrap;
			border-bottom: 1px solid #c3c3c3;

			@include mq(sm2) {
				flex-direction: column;
				border-bottom: none;
				border-top: 1px solid #c3c3c3;
			}

			dt,
			dd {
				padding: 25px 0;
				border-top: 1px solid #c3c3c3;

				@include mq(sm2) {
					padding: 0;
					border-top: none;
				}
			}
			dt {
				width: 170px;
				color: $orange;
				display: flex;
				align-items: center;

				@include mq(sm2) {
					width: 100%;
					padding: 17px 0 5px;
				}
			}
			dd {
				width: calc(100% - 170px);

				@include mq(sm2) {
					width: 100%;
					border-bottom: 1px solid #c3c3c3;
					padding: 0 0 17px;
					font-size: vw2(14);
				}

				ul {
					display: flex;
					flex-wrap: wrap;
					gap: 20px 30px;

					@include mq(sm2) {
						gap: 5px 30px;
					}

					> li {
						label {
							display: block;
							cursor: pointer;

							input[type="checkbox"] {
								position: relative;
								width: 16px;
								height: 16px;
								appearance: none;
								background: #ffffff;
								border: solid 1px #707070;
								box-shadow: 0 0 0 0 transparent;
								transition-duration: 0.2s;
								transition-property: background-color, border, box-shadow, color;
								margin-right: 7px;
								position: relative;
								top: -1px;

								/* 強制カラーモードが有効のときは、既定のシステムカラー値を設定して、トランジションを無効にする */
								@media (forced-colors: active) {
									transition: none;
									border-color: CanvasText;
								}

								/* アイコン：選択済みの見た目 */
								&:checked {
									background: $orange; /* 紫色 */
									border-color: $orange; /* 紫色 */

									/* 強制カラーモードが有効のときは、システムカラーを設定 */
									@media (forced-colors: active) {
										background-color: Canvas;
										border-color: CanvasText;
									}
								}

								/* アイコン：活性時のホバー演出 */
								&:hover:enabled {
									/* 強制カラーモードが無効かつ、ホバーが有効のときは、ホバー演出を追加 */
									@media (forced-colors: none) and (any-hover: hover) {
										border-color: $orange; /* 紫色 */
										box-shadow: 0 0 0 4px #d2ceff; /* 薄い紫色 */
									}
								}

								/* チェックマーク */
								&::before {
									position: absolute;
									inset: 0;
									display: block;
									width: 100%;
									height: 100%;
									content: "";
									opacity: 0;
									mask-image: url("../images/common/icon-check.svg");
									mask-repeat: no-repeat;
									mask-size: contain;
									background-color: #ffffff;
									transition: opacity 0.2s;
								}
								/* 強制カラーモードが有効のときは、既定のシステムカラー値を設定して、トランジションを無効にする */
								@media (forced-colors: active) {
									&::before {
										transition: none;
										background-color: CanvasText;
									}
								}

								/* チェックマーク：選択済みの見た目 */
								&:checked::before {
									opacity: 1;
								}
							}
						}
					}
				}
			}
		}
		&__btn {
			padding-top: 35px;

			@include mq(sm2) {
				padding-top: 32px;
			}

			.submit {
				margin: 0 auto;
				width: 264px;
				height: 58px;
				display: flex;
				justify-content: center;
				align-items: center;
				background: $orange url(../images/common/arrow-right.svg) no-repeat right 20px center / 21px auto;
				color: #fff;
				border-radius: 100vh;
				text-align: center;
				transition: $transition;
				&:hover {
					opacity: 0.5;
				}
			}
		}
	}
	&-header {
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		font-family: $font-a;
		font-size: 20px;
		margin-bottom: 25px;
		gap: 20px;

		@include mq(sm2) {
			margin-bottom: 20px;
		}
		.total {
			flex-shrink: 0;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 35px;

		@include mq(sm2) {
			gap: 30px;
		}
	}
	&__item {
		width: calc((100% - 70px) / 3);
		background: #fff;
		border-radius: 10px;
		padding: 22px;
		display: block;
		color: #2b5d55;
		position: relative;
		.category {
			background: #fff;
			color: $green;
			position: absolute;
			left: 0;
			top: 0;
			border-radius: 10px 0 10px 0;
			padding: 5px 15px;
			font-size: 12px;
			font-weight: bold;
		}
		.img {
			margin-bottom: 20px;
			width: 100%;
			height: auto;
			aspect-ratio: 292/208;

			img {
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
		.txt {
			.ttl {
				font-size: 18px;
			}
			.row-wrap {
				border-top: 1px solid #d9d9d9;
				.row {
					border-bottom: 1px solid #d9d9d9;
					display: flex;
					padding: 10px 0;
					.term {
						width: 95px;
					}
					.desc {
						width: calc(100% - 95px);
					}
				}
			}
			.btn {
				margin-top: 20px;
				a {
					background: $orange;
					color: #fff;
					display: flex;
					justify-content: center;
					align-items: center;
					text-align: center;
					min-width: 300px;
					height: 53px;
					border-radius: 100vh;
					position: relative;

					@include mq(sm2) {
						min-width: 100%;
					}

					&:after {
						content: "";
						display: block;
						width: 17px;
						height: 16px;
						background: url(../images/common/arrow-cbtn.svg) no-repeat center / contain;
						position: absolute;
						right: 15px;
						top: 50%;
						transform: translate(0, -50%);
					}
					&:hover {
						opacity: 1;
						background: $green;
					}
				}
			}
		}

		@include mq(sm2) {
			width: 100%;
		}
	}
	&__notfound {
		width: 100%;
		border: 1px solid #fff;
		border-radius: 5px;
		text-align: center;
		padding: 60px 20px;

		.txt {
			font-family: $font-a;
			font-size: 20px;
			margin-bottom: 25px;
		}
		.btn {
			a {
				margin: 0 auto;
				width: 264px;
				height: 58px;
				display: flex;
				justify-content: center;
				align-items: center;
				background: $orange url(../images/common/arrow-right.svg) no-repeat right 20px center / 21px auto;
				color: #fff;
				border-radius: 100vh;
				text-align: center;
				transition: $transition;
				&:hover {
					opacity: 0.5;
				}
			}
		}
	}
}

.single-recruitment {
	margin: 120px 0;
	padding-top: 120px;
	padding-bottom: 140px;

	background: #2b5d55;
	background: linear-gradient(90deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 40px 0 80px;
		padding: 50px $sp-padding 80px;
	}

	.sr-ttl {
		font-size: 18px;
		font-weight: bold;
		margin-bottom: 30px;
	}

	&-header {
		margin-bottom: 100px;
		display: flex;
		flex-direction: row-reverse;

		@include mq(sm2) {
			flex-direction: column-reverse;
			gap: 30px;
			margin-bottom: 80px;
		}

		.img,
		.txt {
			width: 50%;

			@include mq(sm2) {
				width: 100%;
			}
		}
		.txt {
			padding-right: 60px;

			@include mq(sm2) {
				padding-right: 0;
			}

			.status {
				margin-bottom: 10px;
				> span {
					font-size: 12px;
					background: #fff;
					color: $green;
					font-weight: bold;
					border-radius: 5px;
					display: inline-block;
					padding: 0 10px;
				}
			}
			.ttl {
				font-size: 20px;
				margin-bottom: 10px;
			}
		}
		.img {
			img {
				border-radius: 10px;
			}
		}
	}

	&-outline {
		margin-bottom: 120px;

		@include mq(sm2) {
			margin-bottom: 80px;
		}

		.row-wrap {
			border-top: 1px solid #e0e0e0;
			.row {
				border-bottom: 1px solid #e0e0e0;
				padding: 20px 0;
				display: flex;

				@include mq(sm2) {
					flex-direction: column;
				}
				.term {
					font-weight: bold;
					width: 210px;
					padding-right: 20px;
					flex-shrink: 0;

					@include mq(sm2) {
						width: 100%;
					}
				}
				.desc {
					@include mq(sm2) {
						width: 100%;
					}
				}
			}
		}
	}

	&-point {
		margin-bottom: 120px;

		@include mq(sm2) {
			margin-bottom: 80px;
		}

		&__list {
		}
		&__item {
			background: #fff;
			color: $green;
			padding: 30px;
			border-radius: 10px;
			margin-bottom: 30px;

			@include mq(sm2) {
				padding: 20px;
			}

			.ttl {
				font-size: 18px;
				font-weight: bold;

				@include mq(sm2) {
					font-size: 18px;
				}
			}
		}
	}

	&-reason {
		margin-bottom: 80px;

		@include mq(sm2) {
			margin-bottom: 40px;
		}

		&__list {
			display: flex;
			gap: 90px;

			@include mq(sm2) {
				flex-direction: column;
				gap: 60px;
			}
		}
		&__item {
			width: calc((100% - 180px) / 3);
			position: relative;

			@include mq(sm2) {
				width: 100%;
			}

			&:after {
				content: "";
				display: block;
				width: 1px;
				height: 100%;
				/* 線の長さ2pxの線を背景画像とする */
				background-image: linear-gradient(to bottom, #b9b9b9 5px, transparent 5px);
				/* 背景画像の大きさを設定 */
				background-size: 1px 10px;
				/* 背景の開始位置を指定 */
				background-position: right top;
				/* 横方向に繰り返す */
				background-repeat: repeat-y;
				position: absolute;
				right: -45px;
				top: 50%;
				transform: translate(0, -50%);

				@include mq(sm2) {
					width: 100%;
					height: 1px;
					/* 線の長さ2pxの線を背景画像とする */
					background-image: linear-gradient(to right, #b9b9b9 5px, transparent 5px);
					/* 背景画像の大きさを設定 */
					background-size: 10px 1px;
					/* 背景の開始位置を指定 */
					background-position: left bottom;
					/* 横方向に繰り返す */
					background-repeat: repeat-x;
					right: auto;
					left: 50%;
					top: auto;
					bottom: -30px;
					transform: translate(-50%, 0);
				}
			}
			&:last-child {
				&:after {
					display: none;
				}
			}
			.img {
				position: relative;
				margin-bottom: 15px;

				> img {
					border-radius: 10px;
				}
			}
			.txt {
				.ttl {
					font-size: 18px;
					font-weight: bold;
					line-height: 1.67;
					margin-bottom: 10px;
				}
			}
		}
	}

	.entry-btn {
		text-align: center;
		margin-bottom: 40px;

		@include mq(sm2) {
			margin-bottom: 30px;
		}

		a {
			background: $orange;
			display: inline-flex;
			justify-content: space-between;
			align-items: center;
			color: #fff;
			border-radius: 5px;
			width: 430px;
			height: 122px;
			padding: 20px;
			padding-top: 25px;
			text-align: left;

			@include mq(sm2) {
				width: 100%;
				height: 90px;
			}

			.txt {
				.en {
					font-family: $font-a;
					font-size: 40px;
					line-height: 1;
					letter-spacing: 0.1em;

					@include mq(sm2) {
						font-size: 30px;
					}
				}
				.jp {
					font-size: 20px;
					letter-spacing: 0.1em;

					@include mq(sm2) {
						font-size: 15px;
					}
				}
			}
			.arrow {
				width: 36px;
				height: 36px;
				border: 1px solid #fff;
				border-radius: 50%;
				display: flex;
				justify-content: center;
				align-items: center;
				text-align: center;
				line-height: 1;
				font-size: 20px;

				@include mq(sm2) {
					width: 27px;
					height: 27px;
					font-size: 16px;
				}
			}
		}
	}
	.back-btn {
		text-align: center;
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 10px;
			.label {
			}
			.arrow {
				width: 25px;
				height: 25px;
				border: 1px solid $font-color;
				border-radius: 50%;
				display: flex;
				justify-content: center;
				align-items: center;
				text-align: center;
				line-height: 1;
				font-size: 11px;
				img {
					width: 8px;
				}
			}
		}
	}
}

.fix-entry {
	a {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 5px;
		text-align: center;
		width: 116px;
		height: 116px;
		border-radius: 50%;
		background: #fff;
		color: $green;
		position: fixed;
		right: 20px;
		bottom: 20px;
		z-index: 10;
		.txt {
			font-size: 15px;
			line-height: 1.47;
			font-weight: bold;
		}
		.arrow {
			line-height: 1;
		}
	}
}
