@charset "UTF-8";

.newgraduate-intro {
	margin-top: 50px;
	&__ttl {
		font-family: $font-a;
		font-size: 30px;
		border-bottom: 1px solid #fff;
		padding-bottom: 5px;
		margin-bottom: 30px;

		@include mq(sm2) {
			font-size: vw2(20);
			line-height: 1.75;
			padding-bottom: 15px;
			margin-bottom: 20px;
		}
	}
	&__body {
		font-size: 18px;
		line-height: 2.5;

		@include mq(sm2) {
			font-size: vw2(15);
		}
	}
}

.newgraduate-wrap {
	margin-top: 120px;
	padding-bottom: calc(12vw + 120px);
	background: #2b5d55;
	background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 50px 0;
		padding: 50px 0 12vw;
	}
}

.newgraduate-point {
	padding-top: 120px;
	margin: 0px 0 120px;

	@include mq(sm2) {
		margin: 10px 0 80px;
		padding: 0 $sp-padding;
	}

	&-unit-wrap {
		display: flex;
		flex-direction: column;
		gap: 80px;

		@include mq(sm2) {
			gap: 60px;
		}
	}
	&-unit {
		display: grid;
		grid-template-columns: 49% calc(51% - 40px);
		grid-template-rows: auto 1fr;
		grid-column-gap: 40px;
		grid-row-gap: 0px;

		@include mq(sm2) {
			display: block;
		}

		.header {
			@include mq(sm2) {
				text-align: center;
			}

			.number {
				position: relative;
				left: -8px;
				margin-bottom: 12px;
			}
		}
		.img {
			grid-area: 1 / 1 / 3 / 2;

			@include mq(sm2) {
				margin-bottom: 20px;
			}

			img {
				border-radius: 10px;
			}
		}
		.ttl {
			font-family: $font-a;
			font-size: 30px;
			line-height: 1.33;
			margin-bottom: 10px;
			font-weight: 500;

			@include mq(sm2) {
				font-size: vw2(20);
				margin-bottom: 15px;
			}
		}

		&:nth-child(2n) {
			grid-template-columns: calc(51% - 40px) 49%;

			.img {
				grid-area: 1 / 2 / 3 / 3;
			}
		}
	}
}

.newgraduate-personal {
	margin: 120px 0 120px;

	@include mq(sm2) {
		margin: 50px 0 80px;
		padding: 0 $sp-padding;
	}

	.c-ttl {
		.lead {
			@include mq(sm2) {
				text-align: left;
			}
		}
	}

	&-flex {
		display: flex;
		align-items: center;

		@include mq(sm2) {
			display: block;
		}

		.main-list {
			width: 56%;
			display: flex;
			flex-wrap: wrap;

			@include mq(sm2) {
				width: 100%;
			}

			.slick-prev,
			.slick-next {
				@include mq(sm2) {
					width: 12px;
					height: 24px;
				}
			}
			.slick-prev {
				@include mq(sm2) {
					left: -5px;
				}
				&:before {
					background-image: url(../images/slick/left-2.svg);
				}
			}
			.slick-next {
				@include mq(sm2) {
					right: -5px;
				}
				&:before {
					background-image: url(../images/slick/right-2.svg);
				}
			}

			.main-item {
				text-align: center;
				height: 287px;
				width: 287px;
				border: 1px solid #fff;
				border-radius: 50%;
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;

				@include mq(sm2) {
					margin: 0 50px;
				}

				.number {
				}
				.icon {
					height: 87px;
					display: flex;
					justify-content: center;
					align-items: center;
					margin-bottom: 10px;
				}
				.txt {
					font-family: $font-a;
					font-size: 20px;
					line-height: 1.5;

					&.small {
						font-size: 18px;
						line-height: 1.33;
					}
				}
				&.item-2,
				&.item-4 {
					margin-left: -20px;

					@include mq(sm2) {
						margin: 0 50px;
					}
				}
				&.item-3,
				&.item-4 {
					margin-top: -20px;

					@include mq(sm2) {
						margin: 0 50px;
					}
				}
			}
		}
		.sub-list {
			width: 44%;
			border-top: 1px solid #fff;

			@include mq(sm2) {
				width: 100%;
				border-top: none;

				.slick-dots {
					bottom: -10px;
				}
			}

			.sub-item {
				border-bottom: 1px solid #fff;
				display: flex;
				align-items: center;
				padding: 30px 0;

				@include mq(sm2) {
					border-bottom: none;
					margin: 0 10px;
				}

				.number {
					width: 60px;

					@include mq(sm2) {
						display: none;
					}
					img {
						height: 30px;
						width: auto;
					}
				}
				.txt {
					width: calc(100% - 60px);
					font-size: 14px;

					@include mq(sm2) {
						width: 100%;
					}
				}
			}
		}
	}
}

.newgraduate-flow {
	margin: 120px 0 0;

	@include mq(sm2) {
		margin: 50px 0 20px;
		padding: 0 $sp-padding;
		padding-right: 0;
	}
}
