@charset "UTF-8";

.archive-news {
	margin: 120px 0 0;
	padding-top: 120px;
	padding-bottom: calc(12vw + 120px);

	background: #2b5d55;
	background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 40px 0 80px;
		padding: 50px $sp-padding 12vw;
	}

	&-nav {
		margin-bottom: 50px;
		&__list {
			display: flex;
			flex-wrap: wrap;
			gap: 30px;

			@include mq(sm2) {
				gap: 15px;
			}
		}
		&__item {
			width: calc((100% - 60px) / 3);

			@include mq(sm2) {
				width: calc((100% - 15px) / 2);
			}

			a {
				display: flex;
				justify-content: center;
				align-content: center;
				text-align: center;
				padding: 10px;
				border: 1px solid #fff;
				border-radius: 5px;

				&.active {
					background: #fff;
					color: $green;
				}
			}
		}
	}
	&-header {
		display: flex;
		justify-content: space-between;
		font-family: $font-a;
		font-size: 20px;
		margin-bottom: 25px;

		@include mq(sm2) {
			margin-bottom: 20px;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		gap: 50px;

		@include mq(sm2) {
			gap: 30px;
		}
	}
	.index-interview__item {
		width: calc((100% - 100px) / 3);
		margin: 0;

		@include mq(sm2) {
			width: 100%;
		}
	}
}

.col-wrap {
	&.news {
		margin: 120px 0 0;
		padding-top: 120px;
		padding-bottom: calc(12vw + 120px);

		background: #2b5d55;
		background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
		backdrop-filter: blur(30px);

		@include mq(sm2) {
			margin: 40px 0 80px;
			padding: 50px $sp-padding 12vw;
		}
		.col-wrap-flex {
			display: flex;

			@include mq(sm2) {
				flex-direction: column;
				gap: 80px;
			}

			.main-col {
				width: 61%;

				@include mq(sm2) {
					width: 100%;
				}
			}
			.side-col {
				width: 39%;
				padding-left: 90px;

				@include mq(sm2) {
					width: 100%;
					padding-left: 0;
				}

				&__ttl {
					font-family: $font-a;
					font-size: 15px;
					margin-bottom: 15px;
				}

				.side-archive-news {
					&__list {
						display: flex;
						flex-direction: column;
						gap: 20px;

						.index-news__item {
							width: 100%;
						}
					}
				}
			}
		}
	}
}

.single-news {
	&-header {
		margin-bottom: 20px;

		.category {
			background: $orange;
			color: #fff;
			border-radius: 5px;
			padding: 3px 15px;
			margin-bottom: 5px;
			font-size: 12px;
			font-weight: bold;
			display: inline-block;
			&.news {
				background: #a4ae65;
			}
			&.blog {
				background: $orange;
			}
		}
		.date {
			font-size: 15px;
		}
		.postttl {
			font-size: 20px;
		}
	}
	&-eyecatch {
		margin-bottom: 40px;

		@include mq(sm2) {
			margin-bottom: 30px;
		}

		img {
			width: 100%;
			border-radius: 10px;
		}
	}
	&-cnt {
		#toc_container {
			background: #fff;
			color: $green;
			border-radius: 10px;
			padding: 30px 40px;
			margin-bottom: 40px;
			text-align: left;

			@include mq(sm2) {
				margin-bottom: 30px;
			}

			.toc_title {
				margin-bottom: 10px;
				font-weight: bold;
				text-align: left;
			}
			.toc_list {
				text-align: left;
				li {
					margin-bottom: 10px;
					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}
		h2 {
			font-size: 20px;
			border-top: 2px solid #fff;
			border-bottom: 2px solid #fff;
			padding: 20px 0;
			margin-top: 60px;
			margin-bottom: 30px;
		}
		h3 {
			font-size: 18px;
			border-left: 3px solid $orange;
			padding: 0 0 0 15px;
			margin-top: 60px;
			margin-bottom: 20px;
		}
	}
	&-nav {
		display: grid;
		grid-template-columns: 20% 1fr 20%;
		grid-template-rows: 1fr;
		grid-column-gap: 15px;
		grid-row-gap: 0px;
		margin-top: 60px;

		@include mq(sm2) {
			display: grid;
			grid-template-columns: repeat(2, 1fr);
			grid-template-rows: repeat(2, 1fr);
			grid-column-gap: 15px;
			grid-row-gap: 15px;
		}

		a {
			background: #fff;
			border-radius: 5px;
			display: flex;
			justify-content: center;
			align-items: center;
			color: $green;
			padding: 15px 5px;
			gap: 7px;

			.arrow {
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
		.back {
			order: 2;

			@include mq(sm2) {
				grid-area: 2 / 1 / 3 / 3;
			}
		}
		.next,
		.prev {
			a {
				&.empty {
					opacity: 0.5;
					cursor: default;
				}
			}
		}
		.next {
			order: 1;
		}
		.prev {
			order: 3;
		}
	}
}
