@charset "UTF-8";

.ar-wrap {
	margin-top: 120px;
	padding-bottom: calc(12vw + 120px);
	background: #2b5d55;
	background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 50px 0;
		padding: 0px 0 12vw;
	}
}

.ar-anchor {
	padding: 100px 0;

	@include mq(sm2) {
		padding: 70px 0 20px;
	}

	&__list {
		display: flex;
		justify-content: center;
		gap: 50px;

		@include mq(sm2) {
			flex-direction: column;
			padding: 0 $sp-padding;
			gap: 20px;
		}
	}
	&__item {
		width: calc((100% - 50px) / 2);

		@include mq(sm2) {
			width: 100%;
		}
		a {
			border: 1px solid #fff;
			display: inline-flex;
			justify-content: center;
			align-items: center;
			text-align: center;
			width: 100%;
			height: 60px;
			border-radius: 100vh;
			position: relative;

			@include mq(sm2) {
				min-width: 100%;
			}

			&:after {
				content: "";
				display: block;
				width: 17px;
				height: 16px;
				background: url(../images/common/arrow-cbtn.svg) no-repeat center / contain;
				position: absolute;
				right: 25px;
				top: 50%;
				transform: translate(0, -50%) rotate(90deg);
			}
			&:hover {
				opacity: 1;
				background: $orange;
			}
		}
		&.center {
			text-align: center;
		}
	}
}

.ar-flow {
	margin: 120px 0;

	@include mq(sm2) {
		margin: 50px 0 80px;
		padding: 0 $sp-padding;
		padding-right: 0;
	}

	@include mq(sm2) {
		.js-scrollable-wrap {
			white-space: nowrap;
		}
		.js-scrollable {
			word-break: break-all;
			table-layout: fixed;
			display: block;
			overflow: scroll;
			padding-right: $sp-padding;
		}
		.js-scrollable .ar-flow__list {
			width: 1088px;
			max-width: none;
		}
	}

	&__list {
		display: flex;
		gap: 20px;
		padding-top: 30px;

		@include mq(sm2) {
			width: 1090px;
		}
	}
	&__item {
		border: 1px solid #fff;
		background-color: rgba(255, 255, 255, 0.2);
		backdrop-filter: blur(30px);
		border-radius: 10px;
		width: calc((100% - 80px) / 5);
		position: relative;
		padding: 45px 15px 25px;

		.number {
			background: #fff;
			width: 57px;
			height: 57px;
			border-radius: 50%;
			color: $green;
			font-family: $font-a;
			font-size: 25px;
			position: absolute;
			left: 50%;
			top: -28px;
			transform: translate(-50%, 0);
			display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;
		}
		.ttl {
			text-align: center;
			font-family: $font-a;
			font-size: 15px;
			font-weight: 500;
			min-height: 48px;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		.icon {
			text-align: center;
			height: 62px;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 15px;
		}
		.txt {
			font-size: 14px;
			line-height: 1.78;

			@include mq(sm2) {
				white-space: normal;
			}
		}
	}
}

.ar-faq {
	margin: 120px 0 0;

	@include mq(sm2) {
		margin: 50px 0 80px;
		padding: 0 $sp-padding;
	}

	&-inner {
		display: flex;

		@include mq(sm2) {
			display: block;
		}
	}

	.c-ttl {
		width: 222px;

		@include mq(sm2) {
			width: 100%;
		}
	}

	&__list {
		width: calc(100% - 222px);

		@include mq(sm2) {
			width: 100%;
		}
	}
	&__item {
		border: 1px solid #fff;
		background-color: rgba(255, 255, 255, 0.2);
		backdrop-filter: blur(30px);
		border-radius: 10px;
		margin-bottom: 25px;
		.question {
			font-family: $font-a;
			font-size: 18px;
			font-weight: 500;
			padding: 15px 20px;
			padding-right: 42px;
			position: relative;
			cursor: pointer;
			line-height: 1.67;

			@include mq(sm2) {
				font-size: vw2(17);
				padding-right: 32px;
			}

			&:after {
				content: "";
				display: block;
				width: 17px;
				height: 16px;
				background: url(../images/common/arrow-cbtn.svg) no-repeat center / contain;
				position: absolute;
				right: 25px;
				top: 50%;
				transform: translate(0, -50%) rotate(90deg);
				transition: $transition;

				@include mq(sm2) {
					right: 15px;
				}
			}
			&.open {
				&:after {
					transform: translate(0, -50%) rotate(90deg) scaleX(-1);
				}
			}
		}
		.answer {
			display: none;
			font-size: 14px;
			padding: 0px 20px 20px;

			@include mq(sm2) {
				font-size: vw2(14);
			}
		}
	}
}
