@charset "UTF-8";

.workstyle-wrap {
	margin-top: 120px;
	padding-bottom: calc(12vw + 120px);
	background: #2b5d55;
	background: linear-gradient(180deg, rgba(43, 93, 85, 1) 0%, rgba(43, 93, 85, 0) 100%);
	backdrop-filter: blur(30px);

	@include mq(sm2) {
		margin: 50px 0;
		padding: 50px 0 12vw;
	}
}
.workstyle-intro {
	padding: 80px 0;
	text-align: center;

	@include mq(sm2) {
		padding: 10px $sp-padding 80px;
	}

	&__ttl {
		font-family: $font-a;
		font-size: 30px;
		line-height: 1.67;
		font-weight: 500;

		@include mq(sm2) {
			font-size: vw2(20);
		}
	}
}

.workstyle-infographic {
	margin: 0 0 120px;

	@include mq(sm2) {
		padding: 0 $sp-padding;
		margin: 0 0 80px;
	}

	&__ttl {
		font-family: $font-a;
		font-size: 30px;
		line-height: 1.67;
		font-weight: 500;

		@include mq(sm2) {
			font-size: vw2(15);
		}
	}
	.list-1 {
		display: flex;
		border-top: 1px solid #fff;
		padding: 30px 0;
		margin: 0;

		@include mq(sm2) {
			flex-direction: column;
			padding: 0;
		}

		> li {
			width: 33.33%;
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 30px;
			position: relative;
			border-right: 1px solid #fff;
			padding: 20px 0;

			@include mq(sm2) {
				padding: 30px 0;
				width: 100%;
				border-right: none;
				border-bottom: 1px solid #fff;
			}

			&:last-child {
				border-right: none;
			}

			.icon {
				@include mq(sm2) {
					width: 45%;
					display: flex;
					justify-content: center;
					align-items: center;
					padding: 0 15px;
				}
			}
			.txt {
				text-align: left;
				line-height: 1.3;

				@include mq(sm2) {
					width: 55%;
				}

				.ttl {
					font-family: $font-a;
					font-size: 30px;
					font-weight: 500;
					margin: 0;

					@include mq(sm2) {
						font-size: vw2(20);
					}
				}
				.num {
					font-family: $font-a;
					font-size: 55px;
					font-weight: 500;

					@include mq(sm2) {
						font-size: vw2(45);
					}
				}
			}
		}
	}
	.list-2 {
		display: grid;
		grid-template-columns: 40% 60%;
		grid-template-rows: auto 1fr;
		grid-column-gap: 40px;
		grid-row-gap: 0px;
		border-top: 1px solid #fff;
		padding: 30px 0;
		margin: 0;

		@include mq(sm2) {
			display: flex;
			flex-direction: column;
			border-top: none;
			padding-bottom: 0;
		}

		> li {
			padding: 30px 0;
			.ttl {
				font-family: $font-a;
				font-size: 30px;
				font-weight: 500;
				margin: 0;

				@include mq(sm2) {
					font-size: vw2(20);
					text-align: center;
				}
			}
			&.item-1 {
				grid-area: 1 / 1 / 3 / 2;
				border-right: 1px solid #fff;
				position: relative;

				@include mq(sm2) {
					border-right: none;
					border-bottom: 1px solid #fff;
				}

				.txt {
					position: absolute;
					left: 0;
					top: 0;

					@include mq(sm2) {
						width: 100%;
					}

					ul {
						li {
							font-family: $font-a;
							font-size: 25px;
							font-weight: 500;
							line-height: 1.4;

							@include mq(sm2) {
								font-size: vw2(25);
							}

							.num {
								font-family: $font-a;
								font-size: 55px;
								font-weight: 500;

								@include mq(sm2) {
									font-size: vw2(45);
								}
							}
						}
					}
				}
			}
			&.item-2 {
				border-bottom: 1px solid #fff;
				padding-top: 10px;

				@include mq(sm2) {
					padding: 30px 0;
				}

				.ttl {
					text-align: center;
					margin-bottom: 30px;
				}
				ul {
					display: flex;

					@include mq(sm2) {
						flex-wrap: wrap;
						gap: 40px 30px;
					}
					li {
						width: 25%;

						@include mq(sm2) {
							width: calc((100% - 30px) / 2);
						}

						.icon {
							height: 84px;
							display: flex;
							align-items: center;
							justify-content: center;
						}
						.txt {
							text-align: center;
							font-family: $font-a;
							font-size: 25px;
							font-weight: 500;
							line-height: 1.4;

							@include mq(sm2) {
								font-size: vw2(25);
							}
							.num {
								font-family: $font-a;
								font-size: 55px;
								font-weight: 500;

								@include mq(sm2) {
									font-size: vw2(45);
								}
							}
						}
					}
				}
			}
			&.item-3 {
				.ttl {
					text-align: center;
					margin-bottom: 10px;
				}
				ul {
					display: flex;
					justify-content: center;
					align-items: center;
					gap: 40px;

					@include mq(sm2) {
						gap: 20px;
					}
					li {
						text-align: center;

						@include mq(sm2) {
							white-space: nowrap;
						}

						.icon {
							height: 100px;
							display: flex;
							align-items: center;
							justify-content: center;

							img {
								&.icon-6-1 {
									@include mq(sm2) {
										width: vw2(68);
									}
								}
								&.icon-6-2 {
									@include mq(sm2) {
										width: vw2(60);
									}
								}
							}
						}
						.txt {
							font-family: $font-a;
							font-size: 25px;
							font-weight: 500;
							line-height: 1.4;

							@include mq(sm2) {
								font-size: vw2(20);
							}
						}
						&.num {
							font-family: $font-a;
							font-size: 55px;
							font-weight: 500;

							@include mq(sm2) {
								font-size: vw2(45);
							}
						}
					}
				}
			}
		}
	}

	.list-3 {
		display: flex;
		border-top: 1px solid #fff;
		border-bottom: 1px solid #fff;
		padding: 30px 0;
		margin: 0;

		@include mq(sm2) {
			flex-direction: column;
			border-bottom: none;
			padding: 0;
		}

		> li {
			width: 50%;
			position: relative;
			border-right: 1px solid #fff;
			padding: 20px 0;

			@include mq(sm2) {
				width: 100%;
				border-right: none;
				border-bottom: 1px solid #fff;
			}

			&:last-child {
				border-right: none;
			}

			.ttl {
				font-family: $font-a;
				font-size: 30px;
				font-weight: 500;
				margin: 0 0 30px;

				@include mq(sm2) {
					font-size: vw2(20);
					text-align: center;
				}
			}

			&.item-1 {
				padding-right: 50px;

				@include mq(sm2) {
					padding: 30px 0;
				}
			}
			&.item-2 {
				padding-left: 50px;
				@include mq(sm2) {
					padding: 30px 0;
				}
			}
		}
	}
}

.workstyle-benefit {
	margin: 120px 0;
	text-align: center;

	@include mq(sm2) {
		padding: 0 $sp-padding;
		margin: 80px 0;
	}

	.list-1 {
		display: flex;
		flex-wrap: wrap;
		gap: 20px;
		margin: 0 0 20px;
		> li {
			width: calc((100% - 60px) / 4);
			height: 80px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			background: #fff;
			color: $green;
			border-radius: 10px;
			font-size: 18px;
			font-weight: bold;
			line-height: 1.5;

			@include mq(sm2) {
				width: calc((100% - 20px) / 2);
				font-size: vw2(15);
				height: 55px;
			}

			small {
				font-size: 15px;
				font-weight: 500;

				@include mq(sm2) {
					font-size: vw2(15);
				}
			}
		}
	}
	.list-2 {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-template-rows: repeat(2, 1fr);
		grid-column-gap: 20px;
		grid-row-gap: 20px;
		margin: 0 0 20px;

		@include mq(sm2) {
			display: flex;
			flex-direction: column;
			gap: 20px;
		}

		> li {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			background: #fff;
			color: $green;
			border-radius: 10px;
			font-size: 18px;
			font-weight: bold;
			line-height: 1.5;

			@include mq(sm2) {
				font-size: vw2(15);
				min-height: 55px;
			}

			small {
				font-size: 15px;
				font-weight: 500;

				@include mq(sm2) {
					font-size: vw2(15);
				}
			}

			&.item-3 {
				grid-area: 1 / 2 / 3 / 3;
				display: block;
				text-align: left;
				padding: 22px 32px;

				@include mq(sm2) {
					padding: 16px;
				}

				.ttl {
					border-bottom: 1px solid $green;
					padding-bottom: 6px;
					margin-bottom: 10px;
				}
				ul {
					display: flex;
					flex-wrap: wrap;
					margin: 0;

					li {
						font-size: 15px;
						position: relative;
						padding-left: 13px;
						margin: 0 15px 8px 0;

						@include mq(sm2) {
							margin: 0 14px 5px 0;
							font-weight: 500;
							font-size: vw2(15);
						}

						&:before {
							content: "●";
							font-size: 10px;
							position: absolute;
							left: 0;
							top: 3px;
						}
					}
				}
			}
		}
	}

	.list-3 {
		> li {
			background: #fff;
			color: $green;
			border-radius: 10px;
			padding: 22px 32px;
			text-align: left;

			display: grid;
			grid-template-columns: calc(57% - 30px) 43%;
			grid-template-rows: auto 1fr;
			grid-column-gap: 30px;
			grid-row-gap: 0px;

			@include mq(sm2) {
				padding: 16px;
				display: block;
			}

			.ttl {
				border-bottom: 1px solid $green;
				padding-bottom: 6px;
				margin-bottom: 10px;
				font-size: 18px;
				font-weight: bold;

				@include mq(sm2) {
					font-size: vw2(15);
					line-height: 1.5;
					padding-bottom: 12px;
					margin-bottom: 15px;
				}

				small {
					font-size: 15px;
					font-weight: 500;
					margin-left: 15px;

					@include mq(sm2) {
						font-size: vw2(15);
						margin-left: 0;
					}
				}
			}
			.img {
				grid-area: 1 / 2 / 3 / 3;

				@include mq(sm2) {
					margin-bottom: 15px;
				}

				img {
					border-radius: 5px;
				}
			}
			.body {
				line-height: 1.8;

				@include mq(sm2) {
					font-size: vw2(15);
				}
			}
		}
	}
}

.workstyle-careerstep {
	margin: 120px 0;

	@include mq(sm2) {
		margin: 80px 0;
		padding: 0 $sp-padding;
	}

	&__body {
		background: #fff;
		border-radius: 10px;
		text-align: center;
		padding: 100px 0;
		color: $green;
	}
}
